import Parser from '../services/parseSheet';

const KEYS = [
  'speciesName',
  'sex',
  'size',
]

export const FORMATS = [
  { format: 'csv', mimeType: 'text/csv' },// .csv
  { format: 'ods', mimeType: 'application/vnd.oasis.opendocument.spreadsheet' }, // .ods
  { format: 'xls', mimeType: 'application/vnd.ms-excel' }, //.xls Microsoft Excel
  { format: 'xlsx', mimeType: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }, //.xlsx Microsoft Excel (OpenXML)
]

export const acceptedFormats = FORMATS.map(({ format }) => format);

export const acceptedMimeTypes = FORMATS.map(({ mimeType }) => mimeType);

const deserializeData = (data) => data.map(element =>
  Object.keys(element).reduce((object, key, index) => {
    object[KEYS[index]] = element[key];

    return object
  }, {})
)

export const parseFile = async (file) => {
  const parsedData = await Parser.parse(file);
  return (deserializeData(parsedData))
}
